.jspContainer {
    overflow: hidden;
    position: relative
}

.jspPane {
    position: absolute
}

.jspVerticalBar {
    background: red;
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    width: 6px
}

.jspHorizontalBar {
    background: red;
    bottom: 0;
    height: 8px;
    left: 0;
    position: absolute;
    width: 100%
}

.jspCap {
    display: none
}

.jspHorizontalBar .jspCap {
    float: left
}

.jspTrack {
    background: #dde;
    position: relative
}

.jspDrag {
    background: rgba($black, .5);
    cursor: pointer;
    left: 0;
    position: relative;
    top: 0
}

.jspHorizontalBar .jspDrag,
.jspHorizontalBar .jspTrack {
    float: left;
    height: 100%
}

.jspArrow {
    background: #50506d;
    cursor: pointer;
    direction: ltr;
    display: block;
    margin: 0;
    padding: 0;
    text-indent: -20000px
}

.jspDisabled {
    background: #80808d;
    cursor: default
}

.jspVerticalBar .jspArrow {
    height: 16px
}

.jspHorizontalBar .jspArrow {
    float: left;
    height: 100%;
    width: 16px
}

.jspVerticalBar .jspArrow:focus {
    border: 1px solid red;
    outline: 0
}

.jspCorner {
    background: $client-secondary-color;
    float: left;
    height: 100%
}

* html .jspCorner {
    margin: 0 -3px 0 0
}

